@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap);
.todayForecast{display:grid;grid-template-columns:1fr 2fr;grid-template-areas:"city city " "img img " "temp temp" "description description " "wind wind";box-sizing:border-box;width:200px;height:250px;padding:10px 15px;margin:0 auto;border-radius:10px;background-color:#0f0d3a;color:white;font-family:"Roboto"}.todayForecast__name{grid-area:city}.todayForecast__image{grid-area:img;width:60px;height:60px}.todayForecast__temp{grid-area:temp;font-size:32px}.todayForecast__description{grid-area:description}.todayForecast__wind{grid-area:wind;display:flex;justify-content:space-between}

.search{box-sizing:border-box;display:flex;flex-direction:column;justify-content:center;background-color:whitesmoke;width:600px;height:350px;margin:0 auto;border-radius:15px}.search .search__input{box-sizing:border-box;width:300px;padding:10px 10px;margin:10px auto;border-radius:10px;border:none;font-size:16px;box-shadow:0px 0px 15px 4px rgba(0,0,0,0.12);color:#3498db;font-family:"Roboto", sans-serif;font-weight:500}.search .search__input:focus{outline-width:none;outline-offset:none;outline-color:#3498db;outline:2px solid #3498db}.search .search__button{padding:10px 35px;margin:10px auto;border-radius:10px;border:none;box-shadow:0 0 15px 4px rgba(0,0,0,0.12);transition-duration:0.15s;color:#3498db;font-family:"Roboto", sans-serif;font-weight:500;font-size:16px}.search .search__button:hover{background-color:#3498db;color:white}.search .search__button:active{background-color:#56badb;color:white}

.Modal{display:flex;align-items:center;justify-content:center;position:fixed;top:0;left:0;width:100vw;height:100vh;background-color:rgba(0,0,0,0.7)}

.content{display:flex;flex-direction:column;justify-content:center;align-items:center;background-color:whitesmoke;width:600px;height:350px;border-radius:15px}.content .content__button{padding:10px 35px;margin:10px auto;border-radius:10px;border:none;box-shadow:0 0 15px 4px rgba(0,0,0,0.12);transition-duration:0.15s;color:#3498db;font-family:"Roboto", sans-serif;font-weight:500;font-size:16px}.content .content__button:hover{background-color:#3498db;color:white}.content .content__button:active{background-color:#56badb;color:white}.content .content__message{margin-bottom:20px;font-size:24px;font-family:"Roboto";text-transform:uppercase}

